import { useState, useCallback } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import {
  XMarkIcon,
  ArrowPathIcon,
  ArrowLeftIcon,
  ArrowRightIcon,
  CheckIcon,
  DocumentTextIcon,
  CurrencyDollarIcon,
  UserGroupIcon,
} from '@heroicons/react/24/outline'
import { useCreateCase } from '../../hooks/useCases'
import type { ActType } from '../../types'

// Act type options for the form
const actTypeOptions: { value: ActType; label: string; description: string }[] = [
  {
    value: 'purchase_sale',
    label: 'Purchase & Sale',
    description: 'Transfer of property ownership through sale'
  },
  {
    value: 'donation',
    label: 'Donation',
    description: 'Gratuitous transfer of property'
  },
  {
    value: 'exchange',
    label: 'Exchange',
    description: 'Swap of properties between parties'
  },
  {
    value: 'lease',
    label: 'Lease',
    description: 'Rental agreement for property usage'
  },
]

// Step indicator component
function StepIndicator({
  currentStep,
  totalSteps
}: {
  currentStep: number
  totalSteps: number
}) {
  return (
    <div className="flex items-center justify-center gap-2 mb-6">
      {Array.from({ length: totalSteps }, (_, i) => (
        <div
          key={i}
          className={`h-2 rounded-full transition-all duration-300 ${
            i === currentStep
              ? 'w-8 bg-blue-500'
              : i < currentStep
              ? 'w-2 bg-blue-500'
              : 'w-2 bg-gray-300 dark:bg-gray-600'
          }`}
        />
      ))}
    </div>
  )
}

interface CreateCaseModalProps {
  isOpen: boolean
  onClose: () => void
}

interface FormData {
  title: string
  act_type: ActType
  description: string
  price: string
  payment_method: 'full' | 'installments' | ''
  installments_count: string
  notes: string
}

const initialFormData: FormData = {
  title: '',
  act_type: 'purchase_sale',
  description: '',
  price: '',
  payment_method: '',
  installments_count: '',
  notes: '',
}

export default function CreateCaseModal({ isOpen, onClose }: CreateCaseModalProps) {
  const createCaseMutation = useCreateCase()
  const [currentStep, setCurrentStep] = useState(0)
  const [formData, setFormData] = useState<FormData>(initialFormData)
  const [errors, setErrors] = useState<Partial<Record<keyof FormData, string>>>({})

  const totalSteps = 3

  const updateFormData = useCallback((field: keyof FormData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
    // Clear error when field is updated
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }))
    }
  }, [errors])

  const validateStep = useCallback((step: number): boolean => {
    const newErrors: Partial<Record<keyof FormData, string>> = {}

    if (step === 0) {
      if (!formData.title.trim()) {
        newErrors.title = 'Please enter a case title'
      }
    }

    if (step === 1 && formData.act_type === 'purchase_sale') {
      if (!formData.price.trim()) {
        newErrors.price = 'Please enter the sale price'
      } else if (isNaN(parseFloat(formData.price.replace(/[^0-9.,]/g, '').replace(',', '.')))) {
        newErrors.price = 'Please enter a valid price'
      }
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }, [formData])

  const handleNext = useCallback(() => {
    if (validateStep(currentStep)) {
      setCurrentStep(prev => Math.min(prev + 1, totalSteps - 1))
    }
  }, [currentStep, validateStep])

  const handleBack = useCallback(() => {
    setCurrentStep(prev => Math.max(prev - 1, 0))
  }, [])

  const handleCreate = async () => {
    if (!validateStep(currentStep)) return

    try {
      // Parse price to number
      const priceValue = formData.price
        ? parseFloat(formData.price.replace(/[^0-9.,]/g, '').replace(',', '.'))
        : undefined

      await createCaseMutation.mutateAsync({
        title: formData.title.trim(),
        act_type: formData.act_type,
        description: formData.description.trim() || undefined,
        deal_details: priceValue ? {
          price: priceValue,
          payment_method: formData.payment_method || undefined,
          installments_count: formData.installments_count
            ? parseInt(formData.installments_count)
            : undefined,
        } : undefined,
        notes: formData.notes.trim() || undefined,
      })

      // Reset form and close modal on success
      handleClose()
    } catch (err) {
      console.error('Error creating case:', err)
      setErrors({ title: 'Failed to create case. Please try again.' })
    }
  }

  const handleClose = useCallback(() => {
    setFormData(initialFormData)
    setCurrentStep(0)
    setErrors({})
    onClose()
  }, [onClose])

  const handleKeyDown = useCallback((e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      if (currentStep < totalSteps - 1) {
        handleNext()
      } else {
        handleCreate()
      }
    }
  }, [currentStep, handleNext])

  // Step content components
  const renderStepContent = () => {
    switch (currentStep) {
      case 0:
        return (
          <motion.div
            key="step-0"
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -20 }}
            className="space-y-4"
          >
            <div className="text-center mb-6">
              <div className="mx-auto w-12 h-12 rounded-full bg-blue-100 dark:bg-blue-900/30 flex items-center justify-center mb-3">
                <DocumentTextIcon className="w-6 h-6 text-blue-500" />
              </div>
              <h3 className="text-lg font-medium text-gray-900 dark:text-white">
                Basic Information
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                Start by providing the case title and type
              </p>
            </div>

            {/* Case Title */}
            <div>
              <label
                htmlFor="case-title"
                className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1"
              >
                Case Title <span className="text-red-500">*</span>
              </label>
              <input
                id="case-title"
                type="text"
                value={formData.title}
                onChange={(e) => updateFormData('title', e.target.value)}
                onKeyDown={handleKeyDown}
                placeholder="e.g., Property Sale - 123 Main Street"
                className={`input ${errors.title ? 'input-error' : ''}`}
                autoFocus
              />
              {errors.title && (
                <p className="mt-1 text-sm text-red-500">{errors.title}</p>
              )}
            </div>

            {/* Act Type Selection */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                Act Type <span className="text-red-500">*</span>
              </label>
              <div className="grid grid-cols-2 gap-3">
                {actTypeOptions.map((option) => (
                  <button
                    key={option.value}
                    type="button"
                    onClick={() => updateFormData('act_type', option.value)}
                    className={`p-3 rounded-lg border-2 text-left transition-all ${
                      formData.act_type === option.value
                        ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                        : 'border-gray-200 dark:border-gray-700 hover:border-gray-300 dark:hover:border-gray-600'
                    }`}
                  >
                    <div className="font-medium text-gray-900 dark:text-white text-sm">
                      {option.label}
                    </div>
                    <div className="text-xs text-gray-500 dark:text-gray-400 mt-0.5">
                      {option.description}
                    </div>
                  </button>
                ))}
              </div>
            </div>

            {/* Description */}
            <div>
              <label
                htmlFor="case-description"
                className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1"
              >
                Description <span className="text-gray-400">(optional)</span>
              </label>
              <textarea
                id="case-description"
                value={formData.description}
                onChange={(e) => updateFormData('description', e.target.value)}
                placeholder="Brief description of this case..."
                rows={3}
                className="input resize-none"
              />
            </div>
          </motion.div>
        )

      case 1:
        return (
          <motion.div
            key="step-1"
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -20 }}
            className="space-y-4"
          >
            <div className="text-center mb-6">
              <div className="mx-auto w-12 h-12 rounded-full bg-green-100 dark:bg-green-900/30 flex items-center justify-center mb-3">
                <CurrencyDollarIcon className="w-6 h-6 text-green-500" />
              </div>
              <h3 className="text-lg font-medium text-gray-900 dark:text-white">
                Deal Details
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                {formData.act_type === 'purchase_sale'
                  ? 'Enter the sale price and payment terms'
                  : formData.act_type === 'lease'
                  ? 'Enter the rental terms'
                  : 'Enter any financial details'}
              </p>
            </div>

            {/* Price */}
            <div>
              <label
                htmlFor="case-price"
                className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1"
              >
                {formData.act_type === 'lease' ? 'Monthly Rent' : 'Price'}{' '}
                {formData.act_type === 'purchase_sale' && <span className="text-red-500">*</span>}
              </label>
              <div className="relative">
                <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500 dark:text-gray-400">
                  R$
                </span>
                <input
                  id="case-price"
                  type="text"
                  value={formData.price}
                  onChange={(e) => updateFormData('price', e.target.value)}
                  onKeyDown={handleKeyDown}
                  placeholder="0,00"
                  className={`input pl-10 ${errors.price ? 'input-error' : ''}`}
                />
              </div>
              {errors.price && (
                <p className="mt-1 text-sm text-red-500">{errors.price}</p>
              )}
            </div>

            {/* Payment Method */}
            {formData.act_type === 'purchase_sale' && (
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Payment Method
                </label>
                <div className="flex gap-3">
                  <button
                    type="button"
                    onClick={() => updateFormData('payment_method', 'full')}
                    className={`flex-1 p-3 rounded-lg border-2 text-center transition-all ${
                      formData.payment_method === 'full'
                        ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                        : 'border-gray-200 dark:border-gray-700 hover:border-gray-300'
                    }`}
                  >
                    <div className="font-medium text-gray-900 dark:text-white text-sm">
                      Full Payment
                    </div>
                    <div className="text-xs text-gray-500 dark:text-gray-400 mt-0.5">
                      Single payment
                    </div>
                  </button>
                  <button
                    type="button"
                    onClick={() => updateFormData('payment_method', 'installments')}
                    className={`flex-1 p-3 rounded-lg border-2 text-center transition-all ${
                      formData.payment_method === 'installments'
                        ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                        : 'border-gray-200 dark:border-gray-700 hover:border-gray-300'
                    }`}
                  >
                    <div className="font-medium text-gray-900 dark:text-white text-sm">
                      Installments
                    </div>
                    <div className="text-xs text-gray-500 dark:text-gray-400 mt-0.5">
                      Multiple payments
                    </div>
                  </button>
                </div>
              </div>
            )}

            {/* Installments Count */}
            {formData.payment_method === 'installments' && (
              <motion.div
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: 'auto' }}
                exit={{ opacity: 0, height: 0 }}
              >
                <label
                  htmlFor="installments-count"
                  className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1"
                >
                  Number of Installments
                </label>
                <input
                  id="installments-count"
                  type="number"
                  min="2"
                  max="360"
                  value={formData.installments_count}
                  onChange={(e) => updateFormData('installments_count', e.target.value)}
                  onKeyDown={handleKeyDown}
                  placeholder="e.g., 12"
                  className="input"
                />
              </motion.div>
            )}
          </motion.div>
        )

      case 2:
        return (
          <motion.div
            key="step-2"
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -20 }}
            className="space-y-4"
          >
            <div className="text-center mb-6">
              <div className="mx-auto w-12 h-12 rounded-full bg-purple-100 dark:bg-purple-900/30 flex items-center justify-center mb-3">
                <UserGroupIcon className="w-6 h-6 text-purple-500" />
              </div>
              <h3 className="text-lg font-medium text-gray-900 dark:text-white">
                Additional Notes
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                Add any relevant notes or observations
              </p>
            </div>

            {/* Summary */}
            <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-4 space-y-2">
              <h4 className="font-medium text-gray-900 dark:text-white text-sm">
                Case Summary
              </h4>
              <div className="text-sm space-y-1">
                <div className="flex justify-between">
                  <span className="text-gray-500 dark:text-gray-400">Title:</span>
                  <span className="text-gray-900 dark:text-white font-medium">
                    {formData.title || '-'}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-500 dark:text-gray-400">Type:</span>
                  <span className="text-gray-900 dark:text-white">
                    {actTypeOptions.find(o => o.value === formData.act_type)?.label}
                  </span>
                </div>
                {formData.price && (
                  <div className="flex justify-between">
                    <span className="text-gray-500 dark:text-gray-400">Price:</span>
                    <span className="text-gray-900 dark:text-white">
                      R$ {formData.price}
                    </span>
                  </div>
                )}
                {formData.payment_method && (
                  <div className="flex justify-between">
                    <span className="text-gray-500 dark:text-gray-400">Payment:</span>
                    <span className="text-gray-900 dark:text-white">
                      {formData.payment_method === 'full'
                        ? 'Full Payment'
                        : `${formData.installments_count || '?'}x Installments`}
                    </span>
                  </div>
                )}
              </div>
            </div>

            {/* Notes */}
            <div>
              <label
                htmlFor="case-notes"
                className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1"
              >
                Notes <span className="text-gray-400">(optional)</span>
              </label>
              <textarea
                id="case-notes"
                value={formData.notes}
                onChange={(e) => updateFormData('notes', e.target.value)}
                placeholder="Any additional information or special conditions..."
                rows={4}
                className="input resize-none"
              />
            </div>
          </motion.div>
        )

      default:
        return null
    }
  }

  return (
    <AnimatePresence>
      {isOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
          {/* Backdrop */}
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="absolute inset-0 bg-black/50"
            onClick={handleClose}
          />

          {/* Modal Content */}
          <motion.div
            initial={{ opacity: 0, scale: 0.95, y: 10 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.95, y: 10 }}
            transition={{ type: 'spring', damping: 25, stiffness: 300 }}
            className="card relative shadow-xl p-6 max-w-lg w-full max-h-[90vh] overflow-y-auto"
          >
            {/* Modal Header */}
            <div className="flex items-center justify-between mb-2">
              <h2 className="text-lg font-semibold text-gray-900 dark:text-white">
                Create New Case
              </h2>
              <button
                onClick={handleClose}
                className="p-1 rounded-md text-gray-400 hover:text-gray-500 hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
              >
                <XMarkIcon className="w-5 h-5" />
              </button>
            </div>

            {/* Step Indicator */}
            <StepIndicator currentStep={currentStep} totalSteps={totalSteps} />

            {/* Step Content */}
            <AnimatePresence mode="wait">
              {renderStepContent()}
            </AnimatePresence>

            {/* Modal Footer */}
            <div className="mt-6 flex justify-between gap-3">
              <div>
                {currentStep > 0 && (
                  <button
                    onClick={handleBack}
                    className="btn-secondary"
                    disabled={createCaseMutation.isPending}
                  >
                    <ArrowLeftIcon className="w-4 h-4 mr-2" />
                    Back
                  </button>
                )}
              </div>
              <div className="flex gap-3">
                <button
                  onClick={handleClose}
                  className="btn-secondary"
                  disabled={createCaseMutation.isPending}
                >
                  Cancel
                </button>
                {currentStep < totalSteps - 1 ? (
                  <button
                    onClick={handleNext}
                    className="btn-primary"
                  >
                    Next
                    <ArrowRightIcon className="w-4 h-4 ml-2" />
                  </button>
                ) : (
                  <button
                    onClick={handleCreate}
                    className="btn-primary"
                    disabled={createCaseMutation.isPending}
                  >
                    {createCaseMutation.isPending ? (
                      <>
                        <ArrowPathIcon className="w-5 h-5 mr-2 animate-spin" />
                        Creating...
                      </>
                    ) : (
                      <>
                        <CheckIcon className="w-5 h-5 mr-2" />
                        Create Case
                      </>
                    )}
                  </button>
                )}
              </div>
            </div>
          </motion.div>
        </div>
      )}
    </AnimatePresence>
  )
}
